import { useContext, useLayoutEffect } from 'react';

import { useStableUID } from '../../hooks/useStableUID';
import { LoadingDescription } from './DatabricksLoadingDescriptions';
import { ReactInteractionContext } from './ReactInteractionContext';
import { ReactInteractionIDContext } from './ReactInteractionIDContext';

const isSSR = typeof window === 'undefined';
const useLayoutEffectSSRSafe = isSSR ? () => {} : useLayoutEffect;

type ReactInteractionHoldOptions = {
  allowlist?: Array<string>;
};

export function useReactInteractionHold(
  loading: boolean,
  description?: LoadingDescription,
  options?: ReactInteractionHoldOptions,
) {
  const holdUID = useStableUID();
  const interactionID = useContext(ReactInteractionIDContext);
  const interactionTracingContext = useContext(ReactInteractionContext);
  const { allowlist } = options ?? {};

  // Only report loading when we're in the commit phase
  useLayoutEffectSSRSafe(() => {
    interactionTracingContext.trackLoading(
      interactionID,
      holdUID,
      loading ? description || LoadingDescription.GENERIC : null,
      allowlist,
    );
  }, [interactionID, interactionTracingContext, holdUID, loading, description, allowlist]);

  // Clean-up on un-mount or when we change the interaction. Move the loading state to the newly tracked interaction
  useLayoutEffectSSRSafe(
    () => () => {
      interactionTracingContext.trackLoading(interactionID, holdUID, null);
    },
    [interactionID, holdUID, interactionTracingContext],
  );
}
