let UserTimingsV3Supported = false;
try {
  (performance.measure as any)('a', { start: 0, end: 0 });
  performance.clearMeasures('a');
  UserTimingsV3Supported = true;
  // eslint-disable-next-line no-empty
} catch (e) {}

/**
 * Add a visible measure (block/span) in devtools for this duration.
 * This is helpful when trying to validate performance metrics against
 * screenshots.
 */
export function measure(name: string, start: number, end: number) {
  if (UserTimingsV3Supported) {
    // UserTimings V3 shipped a non backwards compatible API
    (performance.measure as any)(name, {
      start,
      end,
    });
  }
}

export const performanceNow =
  typeof performance !== 'undefined' ? performance.now.bind(performance) : Date.now.bind(Date);
