import React from 'react';

import { getCoinFlipABGroup } from './CoinFlipAB';

export function ReactMemoAB<T extends React.ComponentType<any>>(
  expName: string,
  component: T,
): React.MemoExoticComponent<T> | T {
  if (getCoinFlipABGroup(expName)) {
    return React.memo(component);
  }
  return component;
}
