import type { InteractionMetadata } from './DatabricksInteractionMetadata';
import type { InteractionName } from './DatabricksInteractionNames';
import type { Interaction, InteractionType } from './ReactInteractionTracing';
import { InteractionStatus, startInteraction, stopInteraction } from './ReactInteractionTracing';

const activeImperativeInteractions = new Map<InteractionType, Map<InteractionName, Interaction<InteractionName>>>();

export function startImperativeInteraction<T extends InteractionName>(
  type: InteractionType,
  name: T,
  metadata?: InteractionMetadata<T>,
  startTime: number | null = performance.now(),
) {
  const interaction = startInteraction(type, name, metadata, startTime);
  const interactionMapByType =
    activeImperativeInteractions.get(type) ?? new Map<InteractionName, Interaction<InteractionName>>();
  activeImperativeInteractions.set(type, interactionMapByType);
  const prevInteraction = interactionMapByType.get(name);
  interactionMapByType.set(name, interaction);
  if (prevInteraction) {
    stopInteraction(prevInteraction, InteractionStatus.CANCELLED);
  }
  interactionMapByType.set(name, interaction);
}

export function stopImperativeInteraction(
  type: InteractionType,
  name: InteractionName,
  endTimeInitial: number = performance.now(),
): Interaction<InteractionName> | null {
  const interactionMapByType =
    activeImperativeInteractions.get(type) ?? new Map<InteractionName, Interaction<InteractionName>>();
  const interaction = interactionMapByType.get(name);
  if (!interaction) {
    return null;
  }
  stopInteraction(interaction, InteractionStatus.SUCCESSFUL, endTimeInitial);
  return interaction;
}

export function testReset() {
  activeImperativeInteractions.clear();
}
