/**
 * This module serves as the 'public API' for observability.
 */

import type { MetricTag } from './MetricTag';
import type { MetricDefinitions } from './__generated__/MetricDefinitions';

declare global {
  let DATABRICKS_MFE_ID: string | null | undefined;
}

type recordEventFuncType = (metric: string, tags: Record<string, any>, eventData?: string) => void;
type jsExceptionMeasurementTagsFuncType = (additionalTags?: MetricTag) => MetricTag;

let recordEventFunc: recordEventFuncType | undefined;
let jsExceptionMeasurementTagsFunc: jsExceptionMeasurementTagsFuncType = (a) => a ?? {};
const bufferRecordEvent: Array<() => void> = [];

/**
 * Log the error to our error exception service and the Chrome DevTools console.
 *
 * Usage:
 *   * logError(ex);
 *   * logError("Failed to init MFE", ex);
 *   * logError("Unexpected");
 */
export function logError(messageOrError: unknown, error?: Error, additionalTags?: MetricTag) {
  logJSException(messageOrError, error, additionalTags);

  // eslint-disable-next-line no-console -- Accepted usage
  console.error(messageOrError, error);
}

/**
 * Log the error to our error exception service and the Chrome DevTools console.
 *
 * A warning does not raise an ES ticket unlike a logError(...). It should be used as an FYI
 * that certain code paths or recoverable flows are hit. For instance if performance optimizations
 * like preloading aren't working we can use warning observability to track it.
 */
export function logWarning(messageOrError: unknown, error?: unknown, additionalTags?: MetricTag) {
  logJSException(messageOrError, error, { ...additionalTags, jsExceptionSeverity: 'warning' });

  // eslint-disable-next-line no-console -- Accepted usage
  console.warn(messageOrError, error);
}

/**
 * Log an event to logfood under usage_logs.
 *
 * Differs from window.recordEvent in that 1) its always define, 2) buffers before init,
 * 3) reduces window.* API usage which will minimize the MFE API surface.
 */
export function recordEvent(eventName: MetricDefinitions, additionalTags?: MetricTag, eventData?: string) {
  const recordEventImpl = () => {
    recordEventFunc?.(eventName, { mfeId: DATABRICKS_MFE_ID, ...additionalTags }, eventData);
  };
  if (!recordEventFunc) {
    // Bufer for later
    bufferRecordEvent.push(recordEventImpl);
    return;
  }

  recordEventImpl();
}

/**
 * Only use in App init to provide a logger module. recordEvent will buffer until this is called.
 */
export function registerRecordEvent(
  recordEventFuncParam: recordEventFuncType,
  jsExceptionMeasurementTagsFuncParam: jsExceptionMeasurementTagsFuncType,
) {
  recordEventFunc = recordEventFuncParam;
  jsExceptionMeasurementTagsFunc = jsExceptionMeasurementTagsFuncParam;
  // Flush pending startup logs
  bufferRecordEvent.forEach((cb) => cb());
  bufferRecordEvent.length = 0;
}

export function unregisterRecordEvent() {
  recordEventFunc = undefined;
  jsExceptionMeasurementTagsFunc = (a) => a ?? {};
}

function logJSException(messageOrError: unknown, errorIn?: unknown, additionalTags?: MetricTag) {
  let error: Error;
  if (messageOrError instanceof Error) {
    error = messageOrError;
  } else if (errorIn instanceof Error) {
    error = errorIn;
  } else if (!messageOrError && errorIn) {
    error = new Error(String(errorIn));
  } else {
    error = new Error(String(messageOrError));
  }

  let message: string | undefined;
  if (typeof messageOrError === 'string') {
    message = messageOrError;
  } else if (messageOrError instanceof Error) {
    message = messageOrError.message;
  } else {
    message = 'No error message provided';
  }

  const tags = jsExceptionMeasurementTagsFunc({
    eventType: 'jsExceptionV3',
    ...additionalTags,
    jsExceptionMessage: message,
  });

  recordEvent('jsException', tags, error && error.stack && error.stack.toString());
}
